import React from "react";
import Cod from "./Components/Cod";
import "../src/App.css";
import Nav from "./Components/Nav"




class App extends React.Component{

    state = {
      count: 0,
      message: "Pick a Player",
      order: 0
    };

    changeOrder = () =>{
      if(this.state.order>=2){
        this.setState({ order: 0 });
      }
      else{
        this.setState({ order: this.state.order+1 });
      }
    }

    wrongGuess = () =>{
      this.setState({ message: "Wrongo, Start Over" });
    }
    rightGuess = () =>{
      this.setState({ message: "Great! You get a point!" });
    }

    handleIncrement = () => {

      // We always use the setState method to update a component's state
      this.setState({ count: this.state.count + 1 });
    };
    resetScore = () => {

      // We always use the setState method to update a component's state
      this.setState({ count: 0});
    };

render(){
return (
<div>
<Nav state={this.state}/>
<div className="top">
<h1>Clicky Game!</h1>
<h2>Click on a player to earn points, but don't click on any more than once!</h2>
<h2>Your Score is: {this.state.count}</h2>
</div>
 <Cod  changeOrder={this.changeOrder} wrongGuess={this.wrongGuess} rightGuess={this.rightGuess} resetScore={this.resetScore} handleIncrement={this.handleIncrement}  state={this.state} />

</div>
)
  }
}
export default App;
