import React from "react";
var clicked = false;
const Card6 = (props) =>
{
  const checkClick = () =>{
    console.log('Card1 Running');
    if(clicked){
      props.resetScore();
      clicked = false;
      props.wrongGuess();
    }
    else{
      clicked = true;
      props.handleIncrement();
      props.rightGuess();
    }
    props.changeOrder();
  }
  const img ="https://ak-static.cms.nba.com/wp-content/uploads/headshots/nba/latest/260x190/202324.png"
  return(
  <ul className="list-group">
<div onClick={checkClick}>

<img  src={img} alt='THING'/>
</div>
  </ul>

  );
  }
  export default Card6;